import styled from 'emotion/react';

export default styled.select`
  width: 300px;
  box-shadow: 2px -2px 8px 6px lightgrey inset;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 5px;
  max-width: 80vw;
  font-size: 20px;
  padding: 5px;
`;



