import React from 'react';
import styled from 'emotion/react';

const defaultStyle = {
  textAlign: ({ align = 'left' }) => align,
  padding: '20px',
  lineHeight: '2em',
};

const enabledStyle = {
  ...defaultStyle,
  '& > div:nth-child(1)': {
    fontSize: '2em',
    lineHeight: '1em',
    display: 'block',
    textAlign: 'center',
  },
  '& > div:nth - child(2)': {
    display: 'block',
    textAlign: 'center',
    fontSize: '1.25em',
  }
};

export default styled('div')(({ enabled }) => enabled ? enabledStyle : defaultStyle);


